* {
  font-family: Rubik, sans-serif;
  padding: 0;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h6 {
  font-weight: 400;
}

.todo-container {
  display: grid;
  grid-auto-flow: row;
  border: 2px #282c34 solid;
  padding: 15px;
  margin: 10px;
  border-radius: 7px;
}

.todo-parent {
  display: grid;
  grid-gap: 10px;
}

.App {
  margin-top: 40px;
}

@media only screen and (min-width: 800px) {
  .todo-parent,
  .addTodo {
    width: 400px;
  }
}
@media screen and (max-width: 799px) {
  .todo-parent,
  .addTodo {
    width: 90vw;
  }
}
